.solution-banner{
  width: 100%;
  height: 437px;
  background-color: #010101;
  p{
    text-align: center;
    margin: 0px;
  }
}
.solutions-content{
  color: rgb(66,66,66);
  h2{
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    color: rgb(66,66,66);
    margin-top: 57px;
    margin-bottom: 17px;
  }
  p{
    text-align: center;
  }
  .divider-wrap{
    text-align: center;
    margin: 0px;
    .divider{
      width: 40px;
      height: 1px;
      border-bottom: solid 4px rgb(66,66,66);
      display:block;
      margin: 0 auto;
    }
  }
  .subtitle{
    font-size: 16px;
    margin: 28px 0px;;
    height: 14px;
    line-height: 14px;
  }
  .solutions-row{
    width: 962px;
    margin: 48px auto;
    display: block;
    clear: both;
    .solution-card{
      display: inline-block;
      width: 30%;
      margin-right:14px;
      float: left;
      background-color: rgb(244,244,244);
      &:last-child{
        margin-right: 0px;
      }
      .solution-avatar{
        a{
          img{
            width: 100%;
            &:hover{
              opacity: 0.8;
            }
          }
        }
      }
      .solution-content{
        height: 181px;
        padding-left: 10px;
        padding-right:10px;
        .title{
          margin: 27px 0px 18px 0px;
          font-size: 20px;
          font-weight: 200;
          color: rgb(66,66,66);
        }
        .solution-desc{
          color: rgb(66,66,66);
          margin-top: -15px;
        }
      }
      .card-btn{
        padding-top: 6px;
        display: block;
        background-color: #fff;
        padding-bottom: 20px;
        a{
          padding:10px;
          background-color: #cfcfcf;
          font-size: 14px;
          color: #ffffff;
          margin-bottom: 15px;
          &:hover{
            background-color: #f78500;
          }
        }
      }
    }
  }
  .solutions-extra-row{
    display: block;
    padding-top:80px;
    margin-bottom: 280px;
    .extra{
      width: 33.3%;
      height: 150px;
      float: left;
      .the-box{
        width: 100%;
        display: block;
        .box-icon{
          display: block;
          float: left;
          margin-right: 10px;
          width: 60px;
          span{
            font-size: 60px;
            color: #cfcfcf;
            &:hover{
              color: #424242;
            }
          }
        }
        .box-content{
          float: left;
          padding: 10px;
          width: 230px;
          p{
            font-size: 13px;
            text-align: left;
          }
        }
        &:hover{
          color: #424242 !important;
        }
      }
      h3{
        font-size: 16px;
        height: 20px;
        line-height: 20px;
        margin-bottom: 10px;
        padding:0px;
        text-align: left;
      }
      .subtitle{
        font-size: 11px;
        height: 13px;
        line-height: 13px;
        margin: 0px;
        margin-bottom: 10px;
        padding: 0px;
        text-align: left;
      }
    }
  }
}

.col_one_third{
  width: 22.5%;
  float: left;
  border: solid 1px #ccc;
  display: block;
  position: relative;
  margin-right: 2%;
  margin-bottom: 23px;
  margin-top: 10px;
  img{
    width: 100%;
  }
  .border-dotted-box{
    border-top: dotted 1px #ccc;
    display: flex;
    justify-content: space-around;
    height: 120px;
    .pro-name{
      flex: 1 auto;
      color: #333;
      text-decoration: none;
      font-size: 16px;
      line-height: 40px;
      text-align: center;
    }
    .pro-arrow{
      flex: auto;
      color: #333;
      text-decoration: none;
      font-size: 24px;
      line-height: 40px;
      text-align: right;
      padding-right: 16px;
      .fa{
        margin-top: 7px;
      }
    }
  }
}

.border-hover-red{
  &:hover{
    border-color: #ff0000;
  }
}